    // Función para determinar si un curso está aprobado
    const isCourseApproved = (degree, grade) => {
    const masterApprovedGrades = ["A", "B", "C", "TR"];
    const associateBachelorApprovedGrades = ["A", "B", "C", "D", "TR"];
    const diplomaApprovedGrades = ["A", "B", "C", "D", "TR"]; // Nuevas reglas para diplomas

    if (degree === "Master Degree") {
        return masterApprovedGrades.includes(grade);
    } else if (degree === "Associate Degree" || degree === "Bachelor Degree") {
        return associateBachelorApprovedGrades.includes(grade);
    } else if (degree === "Diploma") {
        return diplomaApprovedGrades.includes(grade); // Lógica para diplomas
    }
    return false; // No aprobado si no coincide con ninguna regla
};

const loadCoursesForModal = (studentId,program) => {
     // Filtra las entradas en groupedData que coincidan con el StudentID y el programa
     const studentEntries = Object.values(groupedData).filter(
        entry => entry.StudentId === studentId && entry.Program === program
