    const approvedCredits = studentEntries.reduce((acc, entry) => acc + entry.CreditsApproved * 3, 0);
    const Remaining = totalCredits > 0 
        ? Math.round(((totalCredits - approvedCredits) / totalCredits) * 100) 
        : 100;

    // Agregar valores calculados al objeto
    selectedCourses.value.totalCredits = totalCredits;
    selectedCourses.value.approvedCredits = approvedCredits;
    selectedCourses.value.Remaining = Remaining;

    console.log("Cursos para el modal:", selectedCourses.value);
};



const gradeValues = {
    A: 4,
    B: 3,
    C: 2,
    D: 1,
    F: 0,
    TR: 0 // Transfer no tiene peso
};

const calculateGPA = (courses) => {
    const gradeValues = { A: 4, B: 3, C: 2, D: 1, F: 0 };
    const evaluatedCourses = [];
    const courseMap = {};

    // Filtrar cursos duplicados y mantener la nota más alta
    courses.forEach((course) => {
        const key = course.CourseCode; // O CourseName, según tus datos
        if (!courseMap[key] || gradeValues[course.Grade] > gradeValues[courseMap[key].Grade]) {
            courseMap[key] = course; // Guarda el curso con la nota más alta
        }
    });

    Object.values(courseMap).forEach((course) => {
        if (gradeValues[course.Grade] !== undefined) {
            evaluatedCourses.push(course);
        }
    });

    // Calcular GPA
    const totalGradePoints = evaluatedCourses.reduce((acc, course) => acc + (gradeValues[course.Grade] || 0), 0);
    const gpa = evaluatedCourses.length > 0 ? totalGradePoints / evaluatedCourses.length : 0;

    return {
        gpa: Math.round(gpa * 100) / 100,
        evaluatedCourses: evaluatedCourses.length,
    };
};


const calculateReentryScore = (creditsEarned, totalCredits, gpa,latestCourseDate) => {
    // Calcular la proximidad a la graduación
    const graduationProximity = (creditsEarned / totalCredits) * 100;

    // Escala de puntuación para la proximidad
    const proximityScore = graduationProximity == 100 ? 100 :
                           graduationProximity >= 95 ? 95  :
                           graduationProximity >= 90 ? 90  :
                           graduationProximity >= 70 ? 70  :
                           graduationProximity >= 50 ? 50  :
                           graduationProximity >= 30 ? 30 : 20;

    // Escala de puntuación para el GPA
    const gpaScore = gpa >= 3.8 ? 100 :
                     gpa >= 3.5 ? 80 :
                     gpa >= 3.0 ? 60 :
                     gpa >= 3.0 ? 40 :
                     gpa >= 2.0 ? 20 : 10;

     // Calcular el puntaje basado en la fecha del último curso
    const today = new Date();
    const courseDate = new Date(latestCourseDate);
    const timeDiff = today - courseDate;
    const daysDiff = timeDiff / (1000 * 60 * 60 * 24);

    // Escala de puntuación para la antigüedad del curso
    const dateScore = daysDiff <= 365 ? 100 :      // Último curso dentro del año
                      daysDiff <= 730 ? 80 :       // Último curso dentro de 2 años
                      daysDiff <= 1095 ? 50 : 20;  // Más de 3 años

    // Score final ponderado
    return (0.75 * proximityScore) + (0.15 * gpaScore) + (0.10 * dateScore);
};



const getYearForProgram = (dateToCheck, programLength) => {
    // Obtener el mapa de fechas y los datos de programas desde el store
    const termDateMap = store.getters.getTermDateMap; // Sigue utilizando el store para este mapa
    const programDatesByYear = store.getters.programDatesByYear; // Y también para los datos por año

    // Convierte la fecha de entrada a un objeto Date
    const checkDate = new Date(dateToCheck);
    console.log("Date: "+checkDate);
    console.log("Len:"+programLength);

    // Itera sobre los años disponibles en programDatesByYear
    for (const [year, programs] of Object.entries(programDatesByYear)) {
        // Encuentra el programa con la longitud especificada
        const program = programs.find(p => p["Program Length"] === programLength);

        if (!program) continue; // Si no hay programa con esa longitud, pasa al siguiente año

        // Convierte las fechas de inicio y fin a objetos Date
        const startDate = new Date(program["Initial Date"]);
        const endDate = new Date(program["Final Date"]);

        // Verifica si la fecha está dentro del rango
        if (checkDate >= startDate && checkDate <= endDate) {
            return parseInt(year); // Devuelve el año como número
        }
    }

    // Si no encuentra un año válido, devuelve null
    return null;
};




const getProgramLength = (programName) => {
    
    const programMonthsMap = store.getters.getProgramMonthsMap;
   
    const length = Object.keys(programMonthsMap).find(key =>
        programMonthsMap[key].includes(programName)
    );
    
    if (!length) {
        console.error(`No se encontró la longitud para el programa: ${programName}`);
        return null; // O un valor por defecto
    }

    return length; // Asegúrate de que sea un número
};



const calculateYearToReport = (termStarted, programLength) => {
    const programDatesByYear = store.getters.programDatesByYear || store.state.programDatesByYear;

    if (!programDatesByYear) {
        
        return null;
    }

   

    const termDate = new Date(termStarted);
    if (isNaN(termDate)) {
        
        return null;
    }

    

    for (const [year, programs] of Object.entries(programDatesByYear)) {
   

    programs.forEach(p => {
        
       

        // Verificar manualmente cada condición
        const isLengthMatch = p["Program Length"] == programLength;
        const isAfterInitial = new Date(p["Initial Date"]) <= termDate;
        const isBeforeFinal = new Date(p["Final Date"]) >= termDate;
    });

    const matchedProgram = programs.find(
        p =>
            p["Program Length"] == programLength &&
            new Date(p["Initial Date"]) <= termDate &&
            new Date(p["Final Date"]) >= termDate
    );

    console.log("Programa coincidente:", matchedProgram);

    if (matchedProgram) {
        return parseInt(year);
    }
}


    return parseInt(2016);;
};



const adaptTermStarted = (term) => {
    const termDateMap = store.getters.getTermDateMap;
    const adaptedDate = termDateMap[term];
