    const date = new Date((serial - 25569) * 86400 * 1000); // Convierte días desde 1900 a milisegundos
    const month = String(date.getMonth() + 1).padStart(2, '0'); // Mes (0-11, ajustamos sumando 1)
    const day = String(date.getDate()).padStart(2, '0'); // Día
    const year = date.getFullYear(); // Año
    return `${month}/${day}/${year}`; // Formato MM/DD/YYYY
};

const exportToExcel = () => {
