        "Year To Report": item.YearToReport,
        ReentryScore: item.ReentryScore,
    }));

    // Crear una hoja de trabajo
    const worksheet = XLSX.utils.json_to_sheet(dataToExport);

    // Crear un libro de trabajo y añadir la hoja de trabajo
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Reentry Program");

    // Exportar el archivo
    XLSX.writeFile(workbook, "reentry_program.xlsx");
};




