const store = useStore();

onMounted(() => {
    const defaultFilePath = process.env.NODE_ENV === 'production'
        ? '/GDE3/datos1.xlsx'  // Para producción en GitHub Pages (reemplaza GDE3 por el nombre de tu repo)
        : '/datos1.xlsx';  // Para desarrollo local // Ruta relativa al archivo en el directorio 'public'
    fetch(defaultFilePath)
        .then(response => response.arrayBuffer())
        .then(data => {
            const workbook = XLSX.read(data, { type: 'array' });
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            excelDataTemp.value = XLSX.utils.sheet_to_json(worksheet);
            excelDataOriginal.value = [...excelDataTemp.value];
        });

});

// Encabezados de la tabla con la nueva columna de 'Start Date'
const columns = ref([
    'No',
    'Student',
    'Program',
    'Status',
    'Term Started',
    'Start Date',
    'CEnd Date', // Nueva columna añadida
    'In Time'
]);

const columnsDataProgram = ref([
    'Program',
    'Enrolled as of',
    'Enrolled between',
    'Graduated between',
    'Dropped between',
    'Total Actived',
]);


const columnsDataProgramByDate = ref([
    'Initial Date',
    'Started',
    'Graduated',
    'Dropped',
    'Active',
]);

const report_date = ref("2025-07-01"); // Fecha final seleccionada
const months = ref("");
const program = ref();
const excelDataTemp = ref([]); // Variable temporal para almacenar los datos del Excel
const items = ref([]);  // Datos finales que se mostrarán en la tabla
const itemsData = ref([]);  // Datos finales que se mostrarán en la tabla por programas
const itemsDataByDate = ref([]);  // Datos finales que se mostrarán en la tabla por programas por fecha


const options1 = ref(["41", "24", "20", "15", "14", "12", "8", "6", "5", "4"]);

const programs = computed(() => {
    // Accedemos al objeto del store para obtener la lista completa de programas
    const allPrograms = store.getters.getAllPrograms || [];
    return allPrograms; // Retorna la lista de programas sin incluir "All"
});

const initial_date = ref("2023/07/01");  // Variable reactiva inicial, sin valor calculado
const final_date = ref("2024/06/30");    // Variable reactiva final, sin valor calculado

// Opciones de la tabla
const table_option = ref({
    perPage: 10,
    perPageValues: [5, 10, 20, 50],
    skin: 'table',
    pagination: { nav: 'scroll', chunk: 5 },
    sortable: ['Student', 'Program', 'Status', 'Start Date'],


});
const table_option2 = ref({
    perPage: 10,
    perPageValues: [5, 10, 20, 50],
    skin: 'table',
    pagination: { nav: 'scroll', chunk: 5 },
    sortable: [],
});

const excelDataOriginal = ref([]);  // Variable para almacenar una copia de todos los datos originales del Excel


const formatDate = (dateObj) => {
    // Si el valor es una cadena, conviértelo a un objeto Date
    if (typeof dateObj === 'string') {
        dateObj = new Date(dateObj);
    }

    // Verificar si `dateObj` es una instancia de `Date` y no es `NaN`
    if (!(dateObj instanceof Date) || isNaN(dateObj.getTime())) {
        console.warn('Fecha inválida:', dateObj);
        return ''; // Retornar vacío si no es una fecha válida
    }

    // Formatear la fecha como MM/DD/YYYY
    const month = String(dateObj.getMonth() + 1).padStart(2, '0');
    const day = String(dateObj.getDate()).padStart(2, '0');
    const year = dateObj.getFullYear();
    return `${month}/${day}/${year}`;
};



const generateData = () => {
    items.value = [];  // Limpiar los datos previos
    itemsData.value = [];  // Limpiar los datos previos de la tabla de programas
    const filteredItems = []; // Aquí guardaremos los estudiantes filtrados por programa

    const initialDateObj = new Date(initial_date.value);
    const finalDateObj = new Date(final_date.value);

    const activeBefore = ref(0);

    // Obtener todas las fechas del mapa de términos
    const allTermDates = Object.values(store.getters.getTermDateMap).map(termDateStr => new Date(termDateStr));

    // Filtrar solo las fechas que están dentro del intervalo
    const filteredDates = allTermDates.filter(date => {
        return date >= initialDateObj && date <= finalDateObj;
    });

    // Ordenar las fechas filtradas
    filteredDates.sort((a, b) => a - b);

    // Llenar la tabla con las fechas filtradas
    filteredDates.forEach(date => {
        itemsDataByDate.value.push({
            'Initial Date': date.toISOString().split('T')[0], // Convertir la fecha al formato YYYY-MM-DD
            'Started': 0, // Valores por defecto para empezar
            'Graduated': 0,
            'Dropped': 0,
            'Active': 0
        });
    });



    let selectedPrograms = [];
    if (program.value === "All") {
        selectedPrograms = programs.value.filter(p => p !== "All");
    } else {
        selectedPrograms = [program.value];
    }

    let graduateCount = 0;
    let dropCount = 0;
    let placementCount = 0;  // Inicializar el contador de Placement para cada programa
    let enrolledCount = 0;

    const normalizeDate = (date) => {
                    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
                };

    const excelSerialToDateFormatted = (serial) => {
                        const excelEpoch = new Date(1899, 11, 30);
                        const dateObj = new Date(excelEpoch.getTime() + serial * 86400000);

                        // Solo ajustar si es un serial de Excel, no para fechas normales
                        if (serial >= 60) { 
                            dateObj.setDate(dateObj.getDate() + 1);
                        }

                        return dateObj.toISOString().split('T')[0];
                    }

                    const initialDateNormalized = normalizeDate(initialDateObj);

    if (excelDataTemp.value.length > 0) {
        selectedPrograms.forEach(selectedProgram => {


            // Limpiar el arreglo de graduados al empezar a procesar un nuevo programa
            const graduatedStudents = {};  // Reinicializar para cada programa

            const studentStatusMap = {};

            let contEnroll = 0;

            // Primer paso: recorrer todos los estudiantes en el rango de fechas seleccionado
            excelDataTemp.value.forEach(row => {
                const termStarted = row['Term Started'];
                const startDateStr = store.getters.getTermDateMap[termStarted] || '';

                if (startDateStr) {
                    const startDateObj = new Date(startDateStr);

                    if (startDateObj <= finalDateObj && startDateObj >= initialDateObj  ) {
                        const item = {
                            Student: row['Student'],
                            Program: row['Program'],
                            Status: row['Status'],
                            'Term Started': termStarted,
                            'Start Date': startDateStr,
                            'CEnd Date': row['CEnd Date'], // Nueva columna incorporada
                            'In Time': 'Between'
                        };

                        contEnroll++;

                        const programVersions = store.getters.getProgramsVersions[selectedProgram] || [];
                        if (item.Status !== 'Not Enrolled' && item.Status !== 'No Start' && programVersions.includes(item.Program)) {
                            const studentKey = `${item.Student}-${item.Program}-${item['Start Date']}`; // Agregamos la fecha como parte de la clave

                            if (!studentStatusMap[studentKey]) {
                                studentStatusMap[studentKey] = {
                                    item: item,
                                    Graduated: false,
                                    Dropped: false,
                                    TransferredOut: false,
                                    Enroll: false
                                };
                            }

                            // Marcar los estados
                            if (item.Status === 'Graduated') {
                                studentStatusMap[studentKey].Graduated = true;
                                if (!graduatedStudents[item.Student]) {
                                    graduatedStudents[item.Student] = [];
                                }
                                graduatedStudents[item.Student].push(item);
                            }
                            if (item.Status === 'Dropped') {
                                studentStatusMap[studentKey].Dropped = true;
                            }
                            if (item.Status === 'Transferred Out') {
                                studentStatusMap[studentKey].TransferredOut = true;
                            }
                            if (item.Status === 'Enroll') {
                                studentStatusMap[studentKey].Enroll = true;
                            }
                        }
                    }
                }
            });

           

            // Segunda pasada: verificar si los estudiantes graduados tienen estado "Enroll" después de la fecha final
            Object.keys(graduatedStudents).forEach(studentName => {
                const studentRecords = graduatedStudents[studentName];

                // Aquí verificamos en excelDataOriginal (datos sin filtrar) si tienen un estado Enroll después de la fecha final
                const hasFutureEnroll = excelDataOriginal.value.some(row => {
                    const studentEnrollDate = new Date(row['Start Date']);
                    return row['Student'] === studentName && studentEnrollDate > finalDateObj && row['Status'] === 'Enroll';
                });

                if (hasFutureEnroll) {
                    console.log(`Estudiante que no necesita Placement: ${studentName}`);
                }

                studentRecords.forEach(record => {
                    if (!hasFutureEnroll) {
                        placementCount++;  // Incrementar solo si el estudiante necesita Placement
                    }
                });
            });

            // Aplicar las reglas para cada estudiante por programa
            Object.keys(studentStatusMap).forEach(studentKey => {
                const status = studentStatusMap[studentKey];


              

            const formatDateToYYYYMMDD = (date) => {
                return date instanceof Date && !isNaN(date)
                    ? date.toISOString().split('T')[0]
                    : null;
            };


            const cEndDateStr = String(status.item['CEnd Date'] || ''); // Convertir a cadena para evitar errores
            let cEndDateObj;

                            // Verificar si `CEnd Date` es un número (serial de Excel)
                if (!isNaN(cEndDateStr)) {
                    cEndDateObj = excelSerialToDateFormatted(Number(cEndDateStr));
                } else if (cEndDateStr.includes('Winter') || cEndDateStr.includes('Fall') || cEndDateStr.includes('Summer')) {
                    // Convertir términos académicos a fecha usando el mapa de términos
                    cEndDateObj = store.getters.getTermDateMap[cEndDateStr] || null;
                } else if (cEndDateStr) {
                    // Convertir fecha normal al formato YYYY-MM-DD
                    cEndDateObj = formatDateToYYYYMMDD(new Date(cEndDateStr));
                } else {
                    cEndDateObj = null;
                }

                                // Asignar `CEnd Date` formateado al objeto para mostrar en la tabla
                status.item['CEnd Date'] = cEndDateObj;



               
            

                const initialDateNormalized = normalizeDate(initialDateObj);
                const finalDateNormalized = normalizeDate(finalDateObj);
                const cEndDateNormalized = normalizeDate(new Date(cEndDateObj));
              
                // Validar que CEnd Date esté dentro del rango para el estado Graduated
                const isCEndDateValid = cEndDateNormalized && cEndDateNormalized >= initialDateNormalized && cEndDateNormalized <= finalDateNormalized;

                contEnroll++;
                if (status.Graduated && status.TransferredOut && isCEndDateValid) {
                    graduateCount++;
                    filteredItems.push({
                        ...status.item,
                        Status: 'Graduated',
                        'CEnd Date': formatDate(cEndDateObj) // Añadir CEnd Date al objeto
                    });
                } else if (status.Dropped && status.TransferredOut && isCEndDateValid) {
                    dropCount++;
                    filteredItems.push({ 
                        ...status.item, 
                        Status: 'Dropped',
                        'CEnd Date': formatDate(cEndDateObj)
                    });
                } else if (status.TransferredOut && status.Enroll && isCEndDateValid) {
                    enrolledCount++;
                    filteredItems.push({ 
                                    ...status.item, 
                                    Status: 'Enroll',
                                    'CEnd Date': formatDate(cEndDateObj)
                                });
                } else if (status.TransferredOut && isCEndDateValid) {
                    dropCount++;
                    filteredItems.push({ 
                        ...status.item, 
                        Status: 'Dropped',
                        'CEnd Date': formatDate(cEndDateObj)
                    });
                } else if (status.Graduated && isCEndDateValid ) {
                    graduateCount++;
                    filteredItems.push({
                        ...status.item,
                        Status: 'Graduated',
                        'CEnd Date': formatDate(cEndDateObj) // Añadir CEnd Date al objeto
                    });
                } else if (status.Dropped && isCEndDateValid) {
                    dropCount++;
                    filteredItems.push({ 
                        ...status.item, 
                        Status: 'Dropped',
                        'CEnd Date': formatDate(cEndDateObj)
                    });
                } else if (status.Enroll) {
                    enrolledCount++;
                    filteredItems.push({ 
                                    ...status.item, 
                                    Status: 'Enroll',
                                    'CEnd Date': formatDate(cEndDateObj)
                                });
                }
            });


        });



       

        /////////////////////////////////////////////////////////
        ///Copia

        selectedPrograms.forEach(selectedProgram => {

            let enrolledCount2 = 0;
            let dropCount2 = 0;
            let graduateCount2 = 0;


           
            


            // Limpiar el arreglo de graduados al empezar a procesar un nuevo programa
            const graduatedStudents2 = {};  // Reinicializar para cada programa

            const studentStatusMap2 = {};

            // Primer paso: recorrer todos los estudiantes en el rango de fechas seleccionado
            excelDataTemp.value.forEach(row => {
                const termStarted = row['Term Started'];
                const startDateStr = store.getters.getTermDateMap[termStarted] || '';


               
                
               

                if (startDateStr) {
                    const startDateObj = new Date(startDateStr);

                    if (startDateObj < initialDateObj) {
                        const item = {
                            Student: row['Student'],
                            Program: row['Program'],
                            Status: row['Status'],
                            'Term Started': termStarted,
                            'Start Date': startDateStr,
                            'In Time': 'Before',
                            'CEnd Date': row['CEnd Date'],
                        };



                

                        
                        

                        const programVersions = store.getters.getProgramsVersions[selectedProgram] || [];
                        if (item.Status !== 'Not Enrolled' && item.Status !== 'No Start' && programVersions.includes(item.Program)) {
                            const studentKey = `${item.Student}-${item.Program}-${item['Start Date']}`; // Agregamos la fecha como parte de la clave

                            if (!studentStatusMap2[studentKey]) {
                                studentStatusMap2[studentKey] = {
                                    item: item,
                                    Graduated: false,
                                    Dropped: false,
                                    TransferredOut: false,
                                    Enroll: false
                                };
                            }

                            // Marcar los estados
                            if (item.Status === 'Graduated' ) {
                                studentStatusMap2[studentKey].Graduated = true;
                                if (!graduatedStudents2[item.Student]) {
                                    graduatedStudents2[item.Student] = [];
                                }
                                graduatedStudents2[item.Student].push(item);
                            }
                            if (item.Status === 'Dropped') {
                                studentStatusMap2[studentKey].Dropped = true;
                            }
                            if (item.Status === 'Transferred Out') {
                                studentStatusMap2[studentKey].TransferredOut = true;
                            }
                            if (item.Status === 'Enroll') {
                                studentStatusMap2[studentKey].Enroll = true;
                            }
                        }
                    }
                }
            });

            // Segunda pasada: verificar si los estudiantes graduados tienen estado "Enroll" después de la fecha final
            Object.keys(graduatedStudents2).forEach(studentName => {
                const studentRecords = graduatedStudents2[studentName];

                // Aquí verificamos en excelDataOriginal (datos sin filtrar) si tienen un estado Enroll después de la fecha final
                const hasFutureEnroll = excelDataOriginal.value.some(row => {
                    const studentEnrollDate = new Date(row['Start Date']);
                    return row['Student'] === studentName && studentEnrollDate > finalDateObj && row['Status'] === 'Enroll';
                });


                studentRecords.forEach(record => {
                    if (!hasFutureEnroll) {
                        placementCount++;  // Incrementar solo si el estudiante necesita Placement
                    }
                });
            });

            // Aplicar las reglas para cada estudiante por programa

          


            Object.keys(studentStatusMap2).forEach(studentKey => {
                const status = studentStatusMap2[studentKey];


            const cEndDateStr = status.item ? String(status.item['CEnd Date'] || '') : '';
            let cEndDateObj = null;

            if (cEndDateStr) {
                if (!isNaN(cEndDateStr)) {
                    cEndDateObj = excelSerialToDateFormatted(Number(cEndDateStr));
                } else if (cEndDateStr.includes('Winter') || cEndDateStr.includes('Fall') || cEndDateStr.includes('Summer')) {
                    cEndDateObj = store.getters.getTermDateMap[cEndDateStr] || null;
                } else {
                    const parsedDate = new Date(cEndDateStr);
                    cEndDateObj = !isNaN(parsedDate) ? formatDateToYYYYMMDD(parsedDate) : null;
                }
            }


                  // Validar que CEnd Date esté dentro del rango para el estado Graduated
            const cEndDateNormalized = normalizeDate(new Date(cEndDateObj));
            const initialDateNormalized = normalizeDate(initialDateObj);
            const finalDateNormalized = normalizeDate(finalDateObj);
        
            const isCEndDateValid = cEndDateNormalized && cEndDateNormalized >= initialDateNormalized && cEndDateNormalized <= finalDateNormalized;



                if (status.Graduated && status.TransferredOut  && isCEndDateValid) {
                    graduateCount2++; 
                 
                    filteredItems.push({ ...status.item, Status: 'Graduated','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.Dropped && status.TransferredOut && isCEndDateValid) {
                    dropCount2++;
                    filteredItems.push({ ...status.item, Status: 'Dropped','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.TransferredOut && status.Enroll ) {
                    enrolledCount2++;
                    filteredItems.push({ ...status.item, Status: 'Enroll','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.TransferredOut && isCEndDateValid) {
                    dropCount2++;
                    filteredItems.push({ ...status.item, Status: 'Dropped','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.Graduated && isCEndDateValid) {
                    graduateCount2++;
                    filteredItems.push({ ...status.item, Status: 'Graduated','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.Dropped && isCEndDateValid) {
                    dropCount2++;
                    filteredItems.push({ ...status.item, Status: 'Dropped','CEnd Date': formatDate(cEndDateObj) });
                } else if (status.Enroll) {
                    enrolledCount2++;
                    filteredItems.push({ ...status.item, Status: 'Enroll','CEnd Date': formatDate(cEndDateObj) });
                }
            });



           
            activeBefore.value = enrolledCount2;

            console.log('Enrolled as of:', enrolledCount2, 'Dropped as of:', dropCount2, 'Graduated as of:', graduateCount2);

            itemsData.value.push({
                Program: selectedProgram,
                'Total Actived': enrolledCount2 + enrolledCount,
                'Graduated between': graduateCount + graduateCount2,
                'Dropped between': dropCount + dropCount2,
                'Enrolled between':enrolledCount + graduateCount + dropCount,
                'Enrolled as of': enrolledCount2 + dropCount2 + graduateCount2// Valor de Placement ya corregido
            });
        });

        // Añadir los datos finales del programa a la tabla

        /////////////////////////////////////////////////////////



        filteredItems.forEach((item, index) => {
            item['No'] = index + 1;  // Asigna números consecutivos
        });

        // Mostrar los estudiantes filtrados
        items.value = filteredItems;





        itemsDataByDate.value = [];  // Limpiar los datos previos de la tabla de programas por fechas

        const dateGroupedData = [];  // Arreglo de objetos para agrupar por fechas

        // Recorrer los items filtrados
        filteredItems.forEach(item => {
            const startDate = item['Start Date'];

            // Buscar si ya existe un objeto con la misma fecha
            const existingDateEntry = dateGroupedData.find(entry => entry['Initial Date'] === startDate);

            if (existingDateEntry) {
                // Si la fecha ya existe, actualizamos los contadores
                existingDateEntry.Started++;
                if (item.Status === 'Graduated') {
                    existingDateEntry.Graduated++;
                } else if (item.Status === 'Dropped') {
                    existingDateEntry.Dropped++;
                } else if (item.Status === 'Enroll') {
                    existingDateEntry.Active++;
                }
            } else {
                // Si la fecha no existe, añadimos un nuevo objeto con esa fecha
                dateGroupedData.push({
                    'Initial Date': startDate,
                    'Started': 1, // Empezamos con 1 porque es el primer estudiante en esa fecha
                    'Graduated': item.Status === 'Graduated' ? 1 : 0,
                    'Dropped': item.Status === 'Dropped' ? 1 : 0,
                    'Active': item.Status === 'Enroll' ? 1 : 0
                });
            }
        });

        // Asignar los datos agrupados a la tabla
        itemsDataByDate.value = dateGroupedData;

        // Ordenar itemsDataByDate de menor a mayor por 'Initial Date'
        itemsDataByDate.value.sort((a, b) => new Date(a['Initial Date']) - new Date(b['Initial Date']));





    }
};


